@import url(https://fonts.googleapis.com/css2?family=Libre+Caslon+Display&display=swap);
body {
  margin: 0;
  font-family: 'Libre Caslon Display', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: white;
}

h1 {
  font-size: 4em;
}

.nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  margin-top: 16px;
  width: 100%;
}


@media (max-width: 490px) {
  h1 {
    font-size: 2em;
  }
}

.button {
  background-color: #FF70FE;
  border: 0;
  border-radius: 4px;
  color: white;
  padding: 16px;
  margin: 16px;
}

.button:hover {
  background-color: #c655c5;
}

.emoji {
  font-size: 10em;
  margin: auto 0;
}
.flash-message {
  width: 400px;
  height: 400px;
  position: fixed;
  margin: 0 auto;
  background-color: transparent;
  opacity: 0.9;
  text-align: center;

  -webkit-animation: fadein 1s; /* Safari, Chrome and Opera > 12.1 */ /* Firefox < 16 */ /* Internet Explorer */ /* Opera < 12.1 */
          animation: fadein 1s;
}

@keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 0.9; }
}

/* Firefox < 16 */

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
    from { opacity: 0; }
    to   { opacity: 0.9; }
}

/* Internet Explorer */

/* Opera < 12.1 */

.about {
  width: 75%;
  padding: 8px;
  margin-top: 64px;
  background-color: black;
  color: white;
  opacity: .8;
}

.about a {
  color: white;
}

@media (max-width: 500px) {
  .about {
    margin-top: 8;
  }
}

h2 {
  text-align: center;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  box-sizing: border-box;
  width: 100%;
  margin-top: 16px;
  padding-left: 16px;
  padding-right: 16px;
  font-size: 1.5em;
}

.button-row {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

@media (max-width: 490px) {
  h2 {
    font-size: 1em;
    text-align: justify;
  }
  .button-row {
    flex-direction: column;
  }
}


